                            <td class="align-middle" v-for="(item, index) in HeaderList" :key="index"
                                :class="{ 'table-danger': dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item , 'legend_leaves ' : item}"
                                :title="dataItem['IsValid'] != null && dataItem['IsValid'] != 1 && dataItem['IsValid'].Date == item ? dataItem['IsValid'].Status : ''">
                                <!-- :class="{ 'table-danger': dataItem['IsValid'] !=null && dataItem['IsValid'] !=1 && IsValidDate == item }" -->
                                <input placeholder="00:00" class="form-control text-center" type="text"
