                                <div class="d-none">{{ dataItem[item] }}</div>
                            </td>
                            <td class="align-middle text-center totalvalue">{{ rowSum(dataItem) }}</td>
                            <td class="remove_td single-action text-center"><i class="fa fa-times text-danger d-none"
                                    @click="SingleDeleteData(dataIndex)"></i> </td>
                        </tr>
                    </tbody>
                    <tbody v-if="TimesheetList.length == 0 && BreakList.length == 0">
                        <tr>
                            <td colspan="11" class="text-center">{{ $t('NoRecordfound') }}</td>
                        </tr>
                    </tbody>

                    <tfoot v-if="TimesheetList.length > 0 || BreakList.length > 0">
