                                <td>{{ $t('Total') }}:</td>
                                <td class="remove_td align-middle d-none"></td>
                                <td class="remove_td align-middle d-none"></td>
                                <td class="text-center" v-for="(tData, index) in totalData" :key="index">{{ tData.value
                                    }}</td>
                                <td class="text-center">{{ SumOfFooterRows }}</td>
                                <td class="remove_td"></td>
                            </tr>
                        </div>
                    </tfoot>
                </table>
            </div>
                    </div>
                    <div class="heading-border mb-3 ">
                            <h5 class="h5">
                                <span class="py-2">Post Comment</span>
