</template>
<script>
import DataService from '@/services/DataService';

export const dateFormat = "MM-DD-YYYY"
export default{

    props: {
        Timesheetdata: {
            type: Array,
            required: true
        },
        // UserData: {
        //     type: Array,
        //     required: true
        // },
        SingleUserData:{
            type: Array,
            required: true
        }
    },
    data(){
        return{
            FormSchema: [{
                layoutType: "double",
                Data: [{
                    astype: "TextField",
                    label: this.$t('Title'),
                    name: "Title",
                    value: "",
                    placeholder: "",
                    config: {
                        //onChange: this.checkRuleName,
                    },
                    validationRules: { "required": true, max: 250 },
                },
                {
                    astype: "RadioField",
                    label: this.$t('Visibilty'),
                    name: "Visibilty",
                    value: 1,
                    placeholder: "",
                    visibility: true,
                    validationRules: {
                        "required": false
                    },
                    config: {
                        options: [{ value: 1, name: "Public" }, { value: 0, name: "Private" }],
                        // onChange: this.ManageLimtedDuration,
                    }
                }
                ]
            },
            {
                layoutType: "single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Description'),
                    name: "description",
                    value: "",
                    mode: "tag",
                    placeholder: "",
                    config: {
                        options: [],
                    },
                    validationRules: {
                        "required": true
                    },
                }]
            }
            ],
            buttons:[
            {
                    type: "submit",
                    name: "AddComment",
                    class: "btn btn-success",
                    text: "<i class='fa fa-send pr-2'></i>Post New Comment",
                    isVisible: true,
                },
            ],
            UserData:[],
            modeluser:null,
            objData:{},
            DataRec:[],
            UserIdd:null,
            formattedStartDate:null,
            formattedEndDate:null,
            computedTimesheetList:[],
            BreakList:[],
            LeaveList:[],
            HeaderList:[],
            oldInputValue: [],
        }
    },
    computed: {
        computedTimesheetList() {
            return this.TimesheetList
        },
        computedBreakList() {
            return this.BreakList
        },
        computedLeaveList() {
            return this.LeaveList
        },
        filteredDeletedArray() {
            return this.deletedArray;
        },
        rowSums() {
            return this.TimesheetList.map(dataItem => this.rowSum(dataItem));
        },
        footerSums() {
            return this.totalData.map(dataItem => this.footerSums(dataItem));
        }
    },
    created(){
        debugger;
        var vm = this;
        vm.formatDate();
        vm.FetchData();
        vm.DataRec=[{
        value :this.GetUserInfo.ID,
         name :this.GetUserInfo.Name}
        ],
        vm.modeluser = this.DataRec[0].value
      
        vm.UserData;
      
        //  console.log('Timesheetdata',this.Timesheetdata);
        //this.GetUsersForTimesheet();
        
    },
    methods:{
        
        FetchData: async function () {
debugger;
this.indexEdit = '';
var VueObj = this;
    VueObj.isLoading = true;
    var url = `companyId=${VueObj.GetUserInfo.CompanyId}&userId=${VueObj.GetUserInfo.ID}&dateFrom=${VueObj.formattedstartDate}&dateTo=${VueObj.formattedEndDate}`;
    DataService.ListTimesheetForWeek(url).then(response => {
        if (response.data != null && response.data != "" && response.data.length > 0) {
            let headerList1 = response.data[0]
            VueObj.isLoading = false;
            VueObj.HeaderList = headerList1;
           // VueObj.TotalRecords = response.data.length;
            VueObj.TimesheetList = response.data.filter((item) => item.Type == 'TimeTrack');
            VueObj.BreakList = response.data.filter((item) => item.Type == 'Break');
            VueObj.LeaveList = response.data.filter(item => item.Type == 'Leave');
            VueObj.TimesheetList.map((item, index) => {
                this.$set(item, 'RowNumber', index + 1);
                item.OldTaskDescription = item.TaskDescription
                return item
            });

        } 

    });

},
onSubmit: function(formfields){
    debugger;
    var vm = this;
                var url = `weekDaterange=${vm.SingleUserData.TIMESHEET_NAME}&title=${formfields.Title}&description=${formfields.description}&access=${formfields.Visibilty}&statusCode=`;
                DataService.AddTimesheetComment(url).then(response => {
                    if (response.data == 0) {
                        vm.ShowAlert("Comment SuccessFully added ", 'success', true, vm.$t('Alert'));
                    }
                })

},
rowSum(dataItem) {
                var rowcount = 0;
                return Object.values(dataItem).filter(dataItem => dataItem !== "ProjectId" && dataItem !== "ProjectName" && dataItem !== "TaskDetail" && dataItem != "TaskId" && dataItem !== "RowTotal")
                    .reduce((sum, value) => {
                        const isDatetimeColumn = /^([01]\d|2[0-3]):([0-5]\d)$/.test(value);
                        if (isDatetimeColumn) {
                            let [hours, minutes] = value != null ? value.split(":") : "00:00".split(":");
                            rowcount += parseInt(hours) * 3600 + parseInt(minutes) * 60;
                        }
                        let hours = Math.floor(rowcount / 3600);
                        let minutes = Math.floor((rowcount % 3600) / 60);
                        sum = String(hours).padStart(2, "0") + ":" + String(minutes).padStart(2, "0");

                        return sum;
                    }, 0);
            },
setOldValue(index, event) {
                let obj = this.oldInputValue.find((item) => item.index === index)
                if (!obj) {
                    this.oldInputValue.push({
                        value: event.target.value,
                        index: index
                    })
                }

            },
        formatDate() {
            debugger;
      const startDate = new Date(this.Timesheetdata.START_TIME);
      const EndDate = new Date(this.Timesheetdata.TRACK_STOP_DATE);
      // Format the date as MM/DD/YY
      const options = { month: '2-digit', day: '2-digit', year: '2-digit' };
      this.formattedstartDate = startDate.toLocaleDateString('en-US', options);
      this.formattedEndDate = EndDate.toLocaleDateString('en-US', options);
     
    },
        GetListData(){
             var VueObj = this;
            var url = `companyId=${VueObj.GetUserInfo.CompanyId}&userId=${VueObj.modeluser}&dateFrom=${fDate}&dateTo=${tDate}`;
            VueObj.buttonShowHide()
            DataService.ListTimesheetForWeekNew(url).then(response => {
                if (response.data != null && response.data != "" && response.data.length > 0) {
                    let headerList1 = Object.keys(response.data[0]).filter(key => key !== 'Type' && key !== 'ProjectId' && key !== 'ProjectName' && key !== 'TaskId' && key !== 'TaskDetail' && key != 'TaskDescription');
                    VueObj.isLoading = false;
                    var count;
                    this.HeaderList = headerList1;

                    VueObj.TotalRecords = response.data.length;
                    VueObj.TimesheetList = response.data.filter((item) => item.Type == 'TimeTrack');
                    VueObj.BreakList = response.data.filter((item) => item.Type == 'Break');
                    VueObj.LeaveList = response.data.filter(item => item.Type == 'Leave');

                    VueObj.TimesheetList.map((item, index) => {
                        //this.isOldTaskDesc[index] = true;
                        this.$set(item, 'RowNumber', index + 1);
                        item.OldTaskDescription = item.TaskDescription
                        return item
                    });

                    console.log('With Row Number', VueObj.TimesheetList);

                    var scol = 0;
                    var totalSeconds = 0;
                    VueObj.SumRowColTotalList()
                    VueObj.checkHolidayDate()
                } else {
                    VueObj.noRecord = true;
                    VueObj.isLoading = false;
                    VueObj.TimesheetList = [];
                }

            });
        },
        ClosePopup(){
            var vm = this;
            vm.$parent.GeneralTimesheetClosePopup();
            },
        
    }
}
</script>
