<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle  py-2">
                            <h4 name="header" class="modal-title mt-2 pt-1" v-if="UserData != ''">
                                {{ $t('Comments') }} for {{ this.UserData[0].USER_NAME }} {{ $t('TimesheetApprovals') }}
                            </h4>
                            <h4 name="header" class="modal-title mt-2 pt-1" v-else>
                                {{ $t('Comments') }}
                            </h4>
                            <button class="close" v-on:click="closePopup"><em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="heading-border mb-3 ">
                            <h5 class="h5">
                                <span class="py-2">Post Comment</span>
                            </h5>
                        </div>
                        <div class="listing py-3 px-3">
                            <dynamic-form lang="en" ref="PostCommentForm" :buttons="buttons" :schema="FormSchema"
                                v-on:OnSubmit="onSubmit"></dynamic-form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '@/services/DataService';

export default {

    props: {
        SingleUserData: { 
            required: false
        },
        UserData: {
            required: false
        },
        headerValue: {
            type: String,
        }
    },
    data() {
        return {
            FormSchema: [{
                layoutType: "double",
                Data: [{
                    astype: "TextField",
                    label: this.$t('Title'),
                    name: "Title",
                    value: "",
                    placeholder: "",
                    config: {
                        //onChange: this.checkRuleName,
                    },
                    validationRules: { "required": true, max: 250 },
                },
                {
                    astype: "RadioField",
                    label: this.$t('Visibilty'),
                    name: "Visibilty",
                    value: 1,
                    placeholder: "",
                    visibility: true,
                    validationRules: {
                        "required": false
                    },
                    config: {
                        options: [{ value: 1, name: "Public" }, { value: 0, name: "Private" }],
                        // onChange: this.ManageLimtedDuration,
                    }
                }
                ]
            },
            {
                layoutType: "single",
                Data: [{
                    astype: "TextAreaField",
                    label: this.$t('Description'),
                    name: "description",
                    value: "",
                    mode: "tag",
                    placeholder: "",
                    config: {
                        options: [],
                    },
                    validationRules: {
                        "required": true
                    },
                }]
            }
            ],
            buttons: [
                {
                    type: "Cancel",
                    class: "btn btn-danger aSkip",
                    text: "Skip <i class='fa fa-forward pl-2'></i>",
                    isVisible: false,
                    name: "Approve",
                    onClick:this.onSubmit
                },
                {
                    type: "Cancel",
                    class: "btn btn-danger aSkip",
                    text: "Skip <i class='fa fa-forward pl-2'></i>",
                    isVisible: false,
                    name: "Reject",
                    onClick:this.onSubmit
                },
                {
                    type: "submit",
                    name: "AddComment",
                    class: "btn btn-success",
                    text: "<i class='fa fa-send pr-2'></i>Post New Comment",
                    isVisible: false,
                },
                {
                    type: "submit",
                    name: "Approve",
                    class: "btn btn-success",
                    text: "<i class='fa fa-send pr-2'></i>Post New Comment",
                    isVisible: false,
                },
                {
                    type: "submit",
                    name: "Reject",
                    class: "btn btn-success",
                    text: "<i class='fa fa-send pr-2'></i>Post New Comment",
                    isVisible: false,
                }


            ],
            trackId: 0,
            isLoading: false,
            //  isAddAssignDept:false
        }
    },
    created: function () {
        debugger
        var data = this.SingleUserData;
        if (this.headerValue == "Approve") {
            this.buttons[0].isVisible = true;
            this.buttons[1].isVisible = false;
            this.buttons[3].isVisible = true;
            this.buttons[2].isVisible = false;
            this.buttons[4].isVisible = false;
        }
        else if (this.headerValue == "Reject") {
            this.buttons[0].isVisible = false;
            this.buttons[1].isVisible = true;
            this.buttons[3].isVisible = false;
            this.buttons[2].isVisible = false;
            this.buttons[4].isVisible = true;
        }
        else {
            this.buttons[2].isVisible = true;
            this.buttons[0].isVisible = false;
            this.buttons[1].isVisible = false;
            this.buttons[3].isVisible = false;
            this.buttons[4].isVisible = false;
        }
        var vv = this.headerValue;
    },
    methods: {
        closePopup() {
            var vm = this;
            vm.$parent.CloseComment();
        },
        onSubmit: function (formfields, lbl) {
            debugger;
            var vm = this;
            if (  lbl == "AddComment") {
                var url = `weekDaterange=${vm.SingleUserData.TIMESHEET_ID}&title=${formfields.Title}&description=${formfields.description}&access=${formfields.Visibilty}&statusCode=`;
                DataService.AddTimesheetComment(url).then(response => {
                    if (response.data == 0) {
                        vm.ShowAlert("Comment SuccessFully added ", 'success', true, vm.$t('Alert'));
                    }
                })
            }
            else if (formfields.name == "Approve" || lbl == "Approve") {

                vm.confirmR(vm.$t("TimesheetApproved"), true, (vm.$t("Delete") + "  " + vm.$t("Group_Item")), function () {
                    var url = `ids=${vm.UserData[0].TIMESHEET_ID}&statusCode=EXP_APPROVED&Resourcemsg=&approvalTypeId=4&commentTitle=${formfields.Title == undefined? "": formfields.Title}&commentDesc=${formfields.description == undefined? "": formfields.description }&commentAccess=${formfields.Visibilty == undefined? 0: formfields.Visibilty}
                &isForciable=0&isOverridable=0&reimburseBy=`;
                    DataService.AddApproverStatus(url).then(response => {
                        if (response.data.status == 'success') {
                            vm.ShowAlert("Approved SuccessFully added ", 'success', true, vm.$t('Alert'));
                        }
                    })

                })

            }
            else if (formfields.name == "Reject" || lbl == "Reject") {
                vm.confirmR(vm.$t("Timesheetreject"), true, (vm.$t("Delete") + "  " + vm.$t("Group_Item")), function () {
                    var url = `ids=${vm.UserData[0].TIMESHEET_ID}&statusCode=EXP_REJECTED&Resourcemsg=&approvalTypeId=4&commentTitle=${formfields.Title == undefined? "": formfields.Title}&commentDesc=${formfields.description == undefined? "": formfields.description }&commentAccess=${formfields.Visibilty == undefined? 0: formfields.Visibilty}
                    &isForciable=0&isOverridable=0&reimburseBy=`;
                    DataService.AddApproverStatus(url).then(response => {
                        if (response.data.status == 'success') {
                            vm.ShowAlert("SuccessFully Reject", 'success', true, vm.$t('Alert'));
                        }
                    })

                })
               
            }
            vm.closePopup();
        },
    }
}
</script>